.main {
  border-bottom: 2px solid var(--button-color);
  width: 100%;
  height: auto;
  display: flex;
  position: relative;
  justify-content: center;
  margin-bottom: 25px;
}
.mainDropdown {
  border-bottom: none;
  width: 100%;
  height: auto;
  display: flex;
  position: relative;
  justify-content: center;
}
.profile {
  width: 5rem;
  height: 5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  margin-bottom: 5px;
  margin-right: 25px;
  margin-left: 24px;
}
.profilePhoto {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: none;
  margin-bottom: 10px;
  margin-top: 5px;
}
.username {
  font-size: 10px;
  margin-bottom: 15px;
  font-style: normal;
  font-weight: normal;
}
.reviewBox {
  display: flex;
  flex-direction: column;
  width: 20rem;
  height: auto;
}
.reviewBoxMenu {
  display: flex;
  justify-content: space-between;
}
.edit {
  margin-right: 10px;
}

.review {
  width: 100%;
  margin-top: 11px;
  margin-bottom: 20px;
  font-weight: 100;
}
.viewStar {
  margin-bottom: 26px;
}
.writeTime {
  font-size: var(--font-small);
  color: rgb(166 165 165);
}

.dropdownBox {
  width: 30px;
  height: 30px;
  margin-right: 25px;
  margin-left: 15px;
}

.recommentCount {
  font-size: var(--font-small);
  color: lightgray;
  position: absolute;
  bottom: 2.2rem;
  width: 15px;
  height: 13px;
}

.dropdown {
  position: absolute;
  bottom: 1rem;
  width: 15px;
  height: 13px;
  transition: all ease 0.5s;
}

.dropdownSpin {
  position: absolute;
  bottom: 1rem;
  width: 15px;
  height: 13px;
  transition: all ease 0.5s;
  transform: rotate(180deg);
}

.commentMain {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.dropdownMain {
  border-bottom: 2px solid var(--button-color);
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 25px;
}

.inputBox {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-top: 11px;
}
.input {
  width: 16rem;
  height: 34px;
  background-color: var(--input-color);
  border: none;
  border-radius: 3px;
  margin-bottom: 30px;
  font-size: 15px;
  padding-left: 15px;
}
.button {
  width: 40px;
  height: 40px;
  background-color: var(--button-color);
  margin-left: 10px;
}

.reviewBoxButton {
  display: flex;
  
}
